//사스문법 네스팅문법 컨테이너 안에있는 h4꾸밈
div.container {
  h4 {
    color: rgb(152, 152, 251);
  }

  p {
    color: green;
  }
}

// mixin은 함수
@mixin 함수() {
  background: #ff00009a;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

//scss 문법 익스텐드 my-alert 복붙
.my-alert2 {
  @extend .my-alert;
  background: #f6ff00;
}



// @include 함수() 쓰면 내부내용 복붙됨

.my-alert p {
  margin-bottom: 0
}